import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";

interface IWhitelistProps {
    items: Array<string>;
}

interface IWhitelistState {
    items: Array<string>;
    urlToAdd: string;
    error: string;
    showTooltip: boolean;
}

export class Whitelist extends React.Component<IWhitelistProps, IWhitelistState> {
    private validURL: boolean;
    private addButtonRef: HTMLButtonElement;

    constructor(props: IWhitelistProps) {
        super(props);
        this.state = {
            items: props.items || [],
            urlToAdd: "",
            error: "",
            showTooltip: false
        };
        this.validURL = true;
        this.add = this.add.bind(this);
        this.remove = this.remove.bind(this);
        this.updateInput = this.updateInput.bind(this);
        this.listen();
    }

    // !IMPORTANT:
    // The adding process is split in 2 (add, updateList)
    // because items can be added from different sources (here or blocked page)

    private isValidURL(url: string) {
        if (url.length > 0 && !BDTLL.Utils.validURL(url)) {
            return false;
        }

        return true;
    }

    private changeUIValidURL() {
        let text_field = (document.getElementsByClassName("text-field") as HTMLCollectionOf<HTMLElement>)[0];
        let add_field = (document.getElementsByClassName("add-field") as HTMLCollectionOf<HTMLElement>)[0];
        let whitelist = (document.getElementsByClassName("whitelist") as HTMLCollectionOf<HTMLElement>)[0];

        if (this.validURL) {
            text_field.style.borderColor = '';
            add_field.style.marginBottom = '20px';
            whitelist.style.marginTop = '2pt';
        } else {
            text_field.style.borderColor = 'red';
            add_field.style.marginBottom = '0px';
            whitelist.style.marginTop = '2pt';
        }
    }

    add(item: string) {
        this.addButtonRef.blur(); // Unfocus "addButton" after adding a new url in the list.

        this.validURL = this.isValidURL(item);
        this.changeUIValidURL();

        if (!this.validURL) {
            this.setState({});
            return;
        }

        var domain = BDTLL.Utils.extractRootDomain(item);
        // TODO: show error: invalid url
        if (!domain || domain == "")
            return;

        // TODO: show error: already exists
        if (this.state.items.indexOf(domain) > -1)
            return;

        // Check for duplicates when adding a new url when the page is not reloaded.
        if (this.state.items.indexOf(item) > -1)
            return;

        BDTLL.MessageService.messageBackground({
            command: BDTLL.Command.WHITELIST_ADD,
            value: item
        }).then(() => {
            this.updateList(domain, "add");
        }).catch((error) => {
            if (BDTLL.DEBUG_MODE) {
                console.error(error);
            }
        });
    }

    remove(item: string) {
        BDTLL.MessageService.messageBackground({
            command: BDTLL.Command.WHITELIST_REMOVE,
            value: item
        }).then(() => {
            this.updateList(item, "remove");
            this.handleMouseOut();
        });
    }

    updateList(item: string, action: string) {
        this.validURL = this.isValidURL(item);
        this.changeUIValidURL();

        this.setState(prevState => {
            var items = prevState.items;

            if (action == "add")
                items = items.concat(item);
            else if (action == "remove")
                items.splice(prevState.items.indexOf(item), 1);
            return {
                urlToAdd: "",
                items: items
            }
        });
    }

    updateInput(evt: React.FormEvent<HTMLInputElement>) {
        this.setState({
            urlToAdd: evt.currentTarget.value
        });
    }

    async addCurrentWebsiteToList() {
        let url = await BDTLL.Utils.getCurrentTabUrl();
        this.add(url);
    }

    handleMouseIn(event: any) {
        if (event.type === "keypress" || event.type === "mouseout") {
            return;
        }

        let tooltip = document.getElementById("tooltip");
        let top = event.pageY - 60;

        tooltip.style.top = top + 'px';

        this.setState({
            showTooltip: true,
        });
    }

    handleMouseOut() {    
        this.setState({
            showTooltip: false
        });
    }

    listen() {
        BDTLL.MessageService.addListener((message: any, sender: any) => {
            if (message.command == BDTLL.Command.WHITELIST_ADD_FRONTEND) {
                this.add(message.value);
            }
        })
    }

    render() {
        const tooltipStyle = {
            display: this.state.showTooltip ? 'block' : 'none'
        }

        return (
            <div id="whitelist_container">
                <a href="#" id='current_website_add' onClick={this.addCurrentWebsiteToList.bind(this)}>
                    + {BDTLL.Utils.get_localized_text("settings_current_website_add_title")}
                </a>
                
                <div className="add-field-title">{BDTLL.Utils.get_localized_text("settings_add_field_title")}</div>

                <div className="add-field">
                    <input type="text" id="itemToAdd" className="text-field" value={this.state.urlToAdd} onChange={this.updateInput}
                            onKeyPress={(e) => {(e.key === "Enter" ? this.add(this.state.urlToAdd) : null)}} />
                    <button type="button" id="add" className="std-btn"
                        onClick={this.add.bind(this, this.state.urlToAdd)}
                        ref={ (addButton) => this.addButtonRef = addButton }>
                    </button>
                </div>

                <div id="tooltip" className="top">
                    <div className="tooltip-arrow" style={tooltipStyle} />
                    <div className="tooltip-text" style={tooltipStyle}>{BDTLL.Utils.get_localized_text("settings_tooltip_text")}</div>
                </div>

                {this.validURL === false ? <div className="invalid_url">Please enter a valid URL</div> : null}

                <ul className="whitelist">
                {this.state.items && this.state.items.map(item => {
                    return (
                        <li key={item}>
                            <div className="webpage">{item}</div>
                            <button
                                type="button" className="remove"
                                onClick={this.remove.bind(this, item)}
                                onMouseOver={(event) => this.handleMouseIn(event)}
                                onMouseOut={() => this.handleMouseOut()}>
                            </button>
                        </li>
                    );
                })}
                </ul>
            </div>
        );
    }
}