import * as React from 'react'

interface IBuggyComponentProps {
    errorSource: string
}

export class BuggyComponent extends React.Component <IBuggyComponentProps, {}> {
    constructor(props: any) {
        super(props);
    }

    render() {
        // Always throw error here.
        if (1 + 1 == 2) {
            throw new Error("Error source: " + this.props.errorSource);
        }

        return (
            <h1>Buggy component</h1>
        );
    }
}

