import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";
import { SettingsGroup } from "./settingsGroup";
import { Whitelist } from "./whitelitst";
import { FeedbackModal } from "../components/feedbackModal";
import { ErrorBoundary } from "../components/errorBoundary";
import { BuggyComponent } from "../components/buggyComponent";
import Header from "../components/header";


interface ISettingsProps {
    threatFilter: boolean;
    linkScanner: boolean;
    whitelist: Array<string>;
    lastUpdate: string;
}

interface ISettingsState {
    threatFilter: boolean;
    linkScanner: boolean;
    whitelist: Array<string>;
    showErrorMessage: boolean;
}

export class SettingsPage extends React.Component<ISettingsProps, ISettingsState> {
    constructor(props: ISettingsProps) {
        super(props);
        this.state = {
            threatFilter: props.threatFilter,
            linkScanner: props.linkScanner,
            whitelist: props.whitelist,
            showErrorMessage: false
        } as ISettingsState;
    }

    render() {
        return (
            <div className="animate-in">
                <div className="switch_container">
                    <SettingsGroup settings={[{ type: BDTLL.SettingType.THREAT_FILTER,
                                                enabled: this.props.threatFilter },
                                            { type: BDTLL.SettingType.SEARCH_ANALYZER,
                                                enabled: this.props.linkScanner },
                                            { type: BDTLL.SettingType.EXCEPTION_LIST,
                                                enabled: true } ]} />

                    <Whitelist items={this.props.whitelist}/>
                </div>
            </div>
        )
    }
}

// This is used to display the settings page from the extension options link (for chrome) and preferences tab (for firefox).
let tf = BDTLL.MessageService.messageBackground(
    {
        command: BDTLL.Command.GET_SETTING,
        type: BDTLL.SettingType.THREAT_FILTER
    }
);
let ls = BDTLL.MessageService.messageBackground(
    {
        command: BDTLL.Command.GET_SETTING,
        type: BDTLL.SettingType.SEARCH_ANALYZER
    }
);
let whitelist = BDTLL.MessageService.messageBackground(
    {
        command: BDTLL.Command.GET_WHITELIST
    }
);
let lastUpdate = BDTLL.MessageService.messageBackground(
    {
        command: BDTLL.Command.GET_LAST_UPDATE
    }
);

window.addEventListener('DOMContentLoaded', async function() {
    let targetElement = document.getElementById("window_title");
    if (targetElement) {
        ReactDOM.render(
            BDTLL.Utils.get_localized_text("extension_name") as any,
            targetElement
        );
    }

    if (tf === null || tf === undefined ||
        ls === null || ls === undefined ||
        whitelist === null || whitelist === undefined ||
        lastUpdate === null || lastUpdate === undefined) {
        targetElement = document.getElementById("settingsPageView");
        if (targetElement) {
            ReactDOM.render(
                <ErrorBoundary>
                    <BuggyComponent errorSource={"settingsPage.tsx"}></BuggyComponent>
                </ErrorBoundary>,
                targetElement
            );
        }
    } else {
        targetElement = document.getElementById("settingsPageView");
        if (targetElement) {
            ReactDOM.render(
                <div>
                    <ErrorBoundary>
                        <Header/>
                        <SettingsPage threatFilter={await tf} linkScanner={await ls} whitelist={await whitelist} lastUpdate={await lastUpdate}/>
                    </ErrorBoundary>
                </div>,
                targetElement
            );
            document.body.classList.add("animate-in");
        }
    }
})
