import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";

const SettingTypeDetails : {[k: string]: {[k: string]: string}} = {
    [BDTLL.SettingType.THREAT_FILTER]: {
        title: BDTLL.Utils.get_localized_text("settings_threat_title"),
        text: BDTLL.Utils.get_localized_text("settings_threat_text")
    },
    [BDTLL.SettingType.SEARCH_ANALYZER]: {
        title: BDTLL.Utils.get_localized_text("settings_search_title"),
        text: BDTLL.Utils.get_localized_text("settings_search_text")
    }
}

export interface ISettingSwitchProps {
    type: BDTLL.SettingType;
    enabled: boolean;
}

interface ISettingSwitchState {
    title: string;
    text: string;
    enabled: boolean;
}

export class SettingSwitch extends React.Component<ISettingSwitchProps, ISettingSwitchState> {
    constructor(props: ISettingSwitchProps) {
        super(props)

        this.state = {
            enabled: props.enabled,
            ...SettingTypeDetails[props.type]
        } as ISettingSwitchState;

        this.toggleSetting = this.toggleSetting.bind(this);
    }

    setSetting(value: boolean) {
        this.setState({
            enabled: value
        });
    }

    toggleSetting() {
        this.setState(prevState => {
            BDTLL.MessageService.messageBackground({
                command: BDTLL.Command.SET_SETTING,
                type: this.props.type,
                value: !prevState.enabled
            }).then(() => {
                this.setSetting(!prevState.enabled);
            });

            return {
                enabled: prevState.enabled
            }
        });
    }

    render() {
        if (this.props.type !== BDTLL.SettingType.EXCEPTION_LIST) {
            return (
                <div className={"switch_row"}>
                    <div className="switch_name" id={this.props.type+"_title"}> {this.state.title}
                        <div className="switch">
                            <div className="custom-checkbox">
                                <input id={this.props.type+"_check"} type="checkbox" onClick={this.toggleSetting}/>
                                <label className={this.state.enabled?"checked":""} htmlFor={this.props.type+"_check"}></label>
                            </div>
                        </div>
                        <br />
                        <span className="description" id={this.props.type+"_text"}>{this.state.text}</span>
                    </div>
                </div>
            );
        } else {
            return (
                <div className={"switch_row"}>
                    <div className="switch_name" id="whitelist_title">{BDTLL.Utils.get_localized_text("settings_exceptions_title")}
                        <br />
                        <span className="description" id="whitelist_text">
                            {BDTLL.Utils.get_localized_text("text_whitelist_submit")}
                        </span>
                    </div>
                </div>
            );
        }
    }
}